import React from 'react';
import SectionSupTitle from "../UI/SectionSupTitle/SectionSupTitle";

import clock from '../../assets/images/icons/clock.svg'

import './blogDetails.css'
import {Link} from "react-router-dom";

const BlogDetails = ({title, date, posts, postImg, recommendations}) => {

    return (
        <section id={'blog-details'}>
            <div className="container blog-details__container">
                <div className={'blog-details__left'}>
                    <SectionSupTitle title={title}/>
                    <div className={'blog-details__date'}>
                        <img src={clock} alt=""/>
                        <span>{date}</span>
                    </div>
                    <div className={'blog-details__left-post'}>
                        <p>{posts && posts[0]}</p>
                        <div className={'blog-details__left-post-box'}>
                            <img src={postImg} alt="post-img"/>
                        </div>
                        <p>{posts && posts[1]}</p>
                    </div>
                </div>
                <div className={'blog-details__right'}>
                    <SectionSupTitle title={'Читайте так же'}/>
                    <div className={'blog-details__right-row'}>
                        {recommendations?.map(item => {
                            return (
                                <div className="blog__box" key={item.id}>
                                    <img src={item.img} alt="img"/>
                                    <div className={'blog__box-item'}>
                                        <p>{item.date}</p>
                                        <p>{item.title}</p>
                                        <p>{item.description}</p>
                                        <Link
                                            to={`/blog/details/${item.id}`}
                                        >читать →</Link>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                </div>
            </div>
        </section>
    );
};

export default BlogDetails;