import React from 'react';

import './AboutBrands.css'


import img1 from '../../../assets/images/brands/img1.png'
import img2 from '../../../assets/images/brands/img2.png'
import img3 from '../../../assets/images/brands/img3.png'
import img4 from '../../../assets/images/brands/img4.png'
import img5 from '../../../assets/images/brands/img5.png'

const AboutBrands = () => {
    return (
        <section id={'about-brands'}>
            <div className="container">
                <div className="about-brands__row">
                    <img src={img1} alt=""/>
                    <img src={img3} alt=""/>
                    <img src={img4} alt=""/>
                    <img src={img5} alt=""/>
                    <img src={img2} alt=""/>
                </div>
            </div>
        </section>
    );
};

export default AboutBrands;