import React from 'react';
import {Link} from "react-router-dom";

const BlogFeedItem = ({id, img, date, title, description}) => {
    return (
        <div className={'col-3'}>
            <div className="blog__box">
                <img src={img} alt="img"/>
                <div className={'blog__box-item'}>
                    <p>{date}</p>
                    <p>{title}</p>
                    <p>{description}</p>
                    <Link
                        to={`/blog/details/${id}`}
                    >читать →</Link>
                </div>
            </div>
        </div>
    );
};

export default BlogFeedItem;