import React from 'react';

import './ServicesFeed.css'
import SectionSupTitle from "../../UI/SectionSupTitle/SectionSupTitle";
import {servicesDetailsData} from "../../../utils/servicesData";
import ServicesFeedItem from "../ServicesFeedItem/ServicesFeedItem";

const ServicesFeed = () => {

    return (
        <section id={'services-feed'}>
            <div className="container">
                <SectionSupTitle title={'Наши услуги'}/>
                <div className="row services-feed__row">
                    {servicesDetailsData?.map(item => {
                        return (
                            <ServicesFeedItem key={item.id} {...item}/>
                        )
                    })}
                </div>
            </div>
        </section>
    );
};

export default ServicesFeed;