import React from 'react';
import { useNavigate } from "react-router-dom";

import './ServicesWrap.css'

import greenBlock from '../../../assets/images/services/green-block.png'

const ServicesWrap = ({data}) => {

    const navigate = useNavigate();

    const handleNavigate = (id) => {
        navigate(`/services/details/${id}`)
    }

    return (
        <div className={'row'}>
            {data?.map(el => {
                return (
                    <div
                        className={'col-3'} key={el.id}
                        onClick={() => handleNavigate(el.id)}
                    >
                        <div className={'services__box'}>
                            <img src={el.img} alt="img" className={'services__box__img'}/>
                            <p>{el.title}</p>
                        </div>
                    </div>
                )
            })}
            <div className={'col-3'}>
                <div className={'services__link-box'} onClick={() => navigate('/services')}>
                    <img src={greenBlock} alt="img" className={'services__link-img'}/>
                    <span>
                        все услуги →
                    </span>
                </div>
            </div>
        </div>
    );
};


// <Link
//     to={'/services'}
// >
//     все услуги →
// </Link>

export default ServicesWrap;