import React from 'react';
import {useNavigate} from "react-router-dom";

import './ServicesFeedItem.css'

const ServicesFeedItem = ({title, img, id}) => {
    const navigate = useNavigate()

    const handleNavigate = (id) => {
        navigate(`/services/details/${id}`)
    }

    return (
        <div
            className="col-3"
            onClick={()=>handleNavigate(id)}
        >
            <div className="services-feed__box">
                <img src={img} alt="img" className={'services-feed__box__img'}/>
                <p>{title}</p>
            </div>
        </div>
    );
};

export default ServicesFeedItem;