import React from 'react';
import CountUp from "react-countup";

import VisibilitySensor from 'react-visibility-sensor';

const AboutWrap = () => {

    return (
        <div className={'row'}>
            <div className="col-4">
                <VisibilitySensor partialVisibility offset={{ bottom: 200 }}>
                    {({ isVisible }) => (
                        <div className="about-wrap__box">
                            {isVisible ? <CountUp
                                end={10}
                                decimals={3}
                                decimal=','
                            /> : <span>0</span>}
                            <span>клиентов</span>
                        </div>
                    )}
                </VisibilitySensor>
            </div>
            <div className="col-4">
                <VisibilitySensor partialVisibility offset={{ bottom: 200 }}>
                    {({ isVisible }) => (
                        <div className="about-wrap__box">
                            {isVisible ? <CountUp end={ 500} /> :  <span>0</span>}
                            <span>клиентов</span>
                        </div>
                    )}
                </VisibilitySensor>
            </div>
            <div className="col-4">
                <VisibilitySensor partialVisibility offset={{ bottom: 200 }}>
                    {({ isVisible }) => (
                        <div className="about-wrap__box">
                            {isVisible ? <CountUp
                                end={5}
                                decimals={3}
                                decimal=','
                            /> :  <span>0</span>}
                            <span>клиентов</span>
                        </div>
                    )}
                </VisibilitySensor>
            </div>
            <div className="col-4">
                <VisibilitySensor partialVisibility offset={{ bottom: 200 }}>
                    {({ isVisible }) => (
                        <div className="about-wrap__box">
                            {isVisible ? <CountUp
                                end={5}
                                decimals={3}
                                decimal=','
                            /> :  <span>0</span>}
                            <span>клиентов</span>
                        </div>
                    )}
                </VisibilitySensor>
            </div>
        </div>
    );
};

export default AboutWrap;