import React from 'react';

import poster from '../../assets/images/poster/about-poster.png'

import './Poster.css';


const Poster = () => {
    return (
        <section id={'about-poster'} className='pt-100px'>
            <div
                className={'poster-backgroundImage'}
                style={{backgroundImage: `url(${poster})`}}
            >
                <div className="container">
                    <div className="poster__row">
                        <h2>Международная Ассоциация Партнёрской <br/> Экономики и Финансов </h2>
                        <span>“Unite - the good in uniting”</span>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Poster;