import React from 'react';
import SectionSupTitle from "../UI/SectionSupTitle/SectionSupTitle";
import ServicesDetailsList from "./ServicesDetailsList/ServicesDetailsList";

import './ServicesDetails.css'

const ServicesDetails = ({description, data}) => {
    return (
        <section id={'services-details'}>
            <div className="container">
                <SectionSupTitle title={description}/>
                <ServicesDetailsList data={data}/>
            </div>
        </section>
    );
};

export default ServicesDetails;