import React from 'react';
import {Link} from "react-router-dom";

import './BlogWrap.css'

const BlogWrap = ({data}) => {
    return (
        <div className={'blog__row'}>
            {data?.map(el => {
                return (
                    <div key={el.id} className={'col-3'}>
                        <div className="blog__box">
                            <img src={el.img} alt="img"/>
                            <div className={'blog__box-item'}>
                                <p>{el.date}</p>
                                <p>{el.title}</p>
                                <p>{el.description}</p>
                                <Link
                                    to={`/blog/details/${el.id}`}
                                >читать →</Link>
                            </div>
                        </div>
                    </div>
                )
            })}
        </div>
    );
};

export default BlogWrap;