import React from 'react';
import {Map, Placemark, YMaps} from '@pbe/react-yandex-maps';

import phone from '../../assets/images/icons/Phone.svg'
import mark from '../../assets/images/icons/mark.svg'
import email from '../../assets/images/icons/Email.svg'

import './Contacts.css';
import SectionSupTitle from "../UI/SectionSupTitle/SectionSupTitle";
const Contacts = () => {
    return (
        <section id={'contacts'}>
            <div className="container contacts__container">
                <div className={'contacts__left'}>
                    <SectionSupTitle title={'Контакты'}/>
                    <div className={'contacts__left-row'}>
                        <div className={'contacts__left-col'}>
                            <img src={mark} alt=""/>
                            <p>г. Бишкек, ул. Абдрахманова 137</p>
                        </div>
                        <div className={'contacts__left-col'}>
                            <img src={phone} alt=""/>
                            <p>+996 (999) 99-99-99</p>
                        </div>
                        <div className={'contacts__left-col'}>
                            <img src={email} alt=""/>
                            <p>corporate@mapef.kg</p>
                        </div>
                    </div>
                </div>
                <YMaps>
                    <div className={'contacts__right'}>
                        <Map
                            defaultState={{ center: [42.818257, 74.558471], zoom: 17 }}
                            width={'100%'}
                            height={'338px'}
                        >
                            <Placemark geometry={[42.818257, 74.558471]} />
                        </Map>
                    </div>
                </YMaps>
            </div>
        </section>
    );
};

export default Contacts;