import React from 'react';

import './CoverWrap.css'

const CoverWrap = ({data}) => {
    return (
        <div className={'row'}>
            {data?.map(el => {
                return (
                    <div className={'col-3'} key={el.id}>
                        <div className="cover__box">
                            <img src={el.img} alt={el.title}/>
                            <p>{el.title}</p>
                        </div>
                    </div>
                )
            })}
        </div>
    );
};

export default CoverWrap;