import React from 'react';

import about from '../../assets/images/about.png'
import SectionTitle from "../UI/SectionTitle/SectionTitle";

import './About.css'
import AboutWrap from "./AboutWrap/AboutWrap";
import {Link} from "react-router-dom";

const About = () => {
    return (
        <section id={'about'}>
            <div className="container">
                <SectionTitle title={'О компании'}/>
                <div className="row about__row">
                    <div className="col-2">
                        <div className="about__box">
                            <p>
                                Противоположная точка зрения подразумевает, что непосредственные участники технического
                                прогресса, превозмогая сложившуюся непростую экономическую ситуацию, своевременно
                                верифицированы. Наше дело не так однозначно, как может показаться: базовый вектор
                                развития играет определяющее значение для глубокомысленных рассуждений.
                            </p>
                            <Link
                                to={'/about'}
                            >
                                ПОДРОБНЕЕ →
                            </Link>
                        </div>
                    </div>
                    <div className="col-2">
                        <div className="about__box">
                            <img src={about} alt="about"/>
                        </div>
                    </div>
                </div>
                <h2 className={'sub-title2'}>За 5  лет нашей работы мы поддерживали:</h2>
                <AboutWrap/>
            </div>
        </section>
    );
};

export default About;