import React from 'react';
import {Link} from "react-scroll";

import './Pagination.css'

import arrow from '../../../assets/images/icons/arrow.svg'

const Pagination = ({pagination, postPerPage, totalPost, currentPage}) => {

    const pageNumber = []
    for (let i = 1; i <= Math.ceil(totalPost / postPerPage); i++) {
        pageNumber.push(i)
    }
    return (
        <div className={'pagination'}>
            <Link
                to={'blog-feed'}
                offset={-120}
                smooth={true}
                onClick={ currentPage === 1 ? null : () => pagination(currentPage - 1)}
                className={'pagination-btn left-arrow'}
            >
                <img src={arrow} alt="icon"/>
            </Link>
            {pageNumber.map((el, index) => {
                return (
                    <Link
                        key={index}
                        to={'blog-feed'}
                        offset={-120}
                        smooth={true}
                        onClick={() => pagination(el)}
                        className={el === currentPage ? 'pagination-btn active-btn' : 'pagination-btn'}
                    >
                        {el}
                    </Link>
                )
            })}
            <Link
                to={'blog-feed'}
                offset={-120}
                smooth={true}
                disabled={currentPage === pageNumber.length}
                onClick={ currentPage === pageNumber.length ? null : () => pagination(currentPage + 1)}
                className={'pagination-btn right-arrow'}
            >
                <img src={arrow} alt="icon"/>
            </Link>
        </div>
    );
};

export default Pagination;