import React from 'react';


import './AboutTasks.css'

const AboutTasks = () => {
    return (
        <section id={'tasks'}>
            <div className="container">
                <div className="tasks__row">
                    <div className="tasks__box">
                        <p>Наши цели</p>
                        <ul>
                            <li>
                                <span>
                                    Представление и защита интересов членов МАПЭФ;
                                </span>
                            </li>
                            <li>
                                <span>
                                    Активное участие в разработке и совершенствовании законодательства об исламской
                                    экономике, финансах, банковском деле и халяль-индустрии на национальном уровне;
                                </span>
                            </li>
                            <li>
                                <span>
                                    Тесное сотрудничество с международными организациями (AAOIFI, CIBAFI, IFSB, OIS/SMIIC);
                                </span>
                            </li>
                            <li>
                                <span>
                                    Содействие в привлечении инвестиций в сектор МСП (международные инвестиции в рамках ИПФ,
                                    государственных программ и грантов);
                                </span>
                            </li>
                            <li>
                                <span>
                                    Продвижение стандартов IPF и ХАЛАЛ на межгосударственном уровне и среди населения;
                                </span>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default AboutTasks;