import React, {useEffect, useState} from 'react';
import Header from "../../components/Header/Header";
import Footer from "../../components/Footer/Footer";
import ServicesDetails from "../../components/ServicesDetails/ServicesDetails";
import {servicesDetailsData} from "../../utils/servicesData";
import {useParams} from "react-router-dom";
import ServicesDetailsPoster from "../../components/ServicesDetails/ServicesDetailsPoster/ServicesDetailsPoster";
import Feedback from "../../components/Feedback/Feedback";

const ServicesDetailsPage = () => {
    const [data, setData] = useState({})
    const {id} = useParams()

    useEffect(() => {
        setData(servicesDetailsData.filter((el) => el.id.toString() === id)[0])
    },[id])

    return (
        <>
            <Header/>
            <ServicesDetailsPoster title={data?.title} description={data?.supTitle} img={data?.posterImg}/>
            <ServicesDetails description={data.description} data={data.array}/>
            <Feedback/>
            <Footer/>
        </>
    );
};

export default ServicesDetailsPage;