import React, {useEffect, useState} from 'react';
import SectionSupTitle from "../../UI/SectionSupTitle/SectionSupTitle";
import {blogDetailsData} from "../../../utils/blogData";
import Pagination from "../../UI/Pagination/Pagination";
import BlogFeedItem from "../blogFeedItem/BlogFeedItem";

import './BlogFeed.css'
const BlogFeed = () => {

    const [post, setPost] = useState([])
    const [currentPage, setCurrentPage] = useState(1)
    const [postPerPage, setPostPerPage] = useState(9)

    useEffect(() => {
        setPost(blogDetailsData)
    }, [])

    const indexOfLastPost = currentPage * postPerPage
    const indexOfFirstPost = indexOfLastPost - postPerPage
    const currentPost = post.slice(indexOfFirstPost, indexOfLastPost)

    const pagination = (pageNumber) => {
        setCurrentPage(pageNumber)
    }

    useEffect(() => {
        if (window.innerWidth < 510) {
            setPostPerPage(4)
        }
        else if (window.innerWidth < 1024) {
            setPostPerPage(6)
        }

    },[])

    return (
        <section id={'blog-feed'}>
            <div className="container">
                <SectionSupTitle title={'Читай в нашем блоге'}/>
                <div className="row blog-feed__row">
                    {currentPost?.map(item => {
                        return (
                            <BlogFeedItem key={item.id} {...item}/>
                        )
                    })}
                </div>
                <Pagination
                    pagination={pagination}
                    postPerPage={postPerPage}
                    totalPost={post.length}
                    currentPage={currentPage}
                />
            </div>
        </section>
    );
};

export default BlogFeed;