import React from 'react';
import FeedbackForm from "./FeedbackForm/FeedbackForm";

import './Feedback.css'

const Feedback = () => {
    return (
        <section id={'feedback'}>
            <div className="container">
                <div className="row">
                    <div className="col-2 feedback__col-left">
                        <p>Задайте вопрос и мы <br/> Вам перезвоним</p>
                        <p>Мы обработаем Ваш вопрос в ближайшем <br/> времени и перезвоним Вам в течении <br/> рабочего дня. </p>
                    </div>
                    <div className="col-2">
                        <FeedbackForm/>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Feedback;