import React from 'react';

import CoverSlider from "./CoverSlider/CoverSlider";
import CoverWrap from "./CoverWrap/CoverWrap";
import {coverWrapData} from "../../utils/coverWrapData";

import './Cover.css'

const Cover = () => {
    return (
        <section id={'cover'}>
            <CoverSlider/>
            <div className="container cover__container">
                <CoverWrap data={coverWrapData}/>
            </div>
        </section>
    );
};

export default Cover;