import React from 'react';

import HeaderNavItem from "../HeaderNavItem/HeaderNavItem";

import './HeaderNav.css'

const HeaderNav = ({data, isVisible}) => {

    return (
        <ul className={isVisible ? 'nav__list' : 'nav__list nav__list-hide'}>
            {data?.map(el => {
                return (
                    <HeaderNavItem key={el.id} {...el}/>
                )
            })}
        </ul>
    );
};

export default HeaderNav;