import React from 'react';

import img from '../../../assets/images/Rectangle.png';

import './AboutFeed.css'

const AboutFeed = () => {
    return (
        <section id={'about-feed'}>
            <div className="container">
                <div className="row">
                    <div className="col-2">
                        <div className="about-feed__box">
                            <h3>О компании</h3>
                            <p>
                                МАПЭФ - это объединение юридических лиц, всесторонняя поддержка, формирование финансовой
                                системы на исламских принципах исламского права, координация сотрудничества между
                                организациями, заинтересованными в развитии исламской экономики, финансов и
                                халяль-индустрии в Кыргызской Республике и на международной арене.
                            </p>
                        </div>
                    </div>
                    <div className="col-2">
                        <div className="about-feed__box">
                            <img src={img} alt="img"/>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default AboutFeed;