import React from 'react';

import {Swiper, SwiperSlide} from "swiper/react";

import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

import {Navigation} from "swiper";


import './LocationWrap.css'

const LocationWrap = ({data}) => {
    return (
        <div className={'row'}>
            <Swiper
                slidesPerView={1}
                spaceBetween={30}
                slidesPerGroup={1}
                loop={true}
                loopFillGroupWithBlank={true}
                navigation={true}
                modules={[Navigation]}
                className="mySwiper"
                breakpoints={{
                    1024: {
                        slidesPerView: 3,
                    },
                    600: {
                        slidesPerView: 2,
                    },

                }}
            >
                {data?.map(el => {
                    return (
                        <SwiperSlide key={el.id}>
                            <div className="location__box">
                                <img src={el.img} alt="img"/>
                                <p>{el.title}</p>
                            </div>
                        </SwiperSlide>
                    )
                })}
                    </Swiper>
                    </div>
                    );
                };

                export default LocationWrap;