import React, {useEffect, useState} from 'react';

import phone from '../../../assets/images/icons/call.svg';
import email from '../../../assets/images/icons/mail.svg';
import location from '../../../assets/images/icons/pin.svg';

import FB from '../../../assets/images/icons/brand/br-fb-sq.svg'
import IG from '../../../assets/images/icons/brand/br-instagram.svg'
import TG from '../../../assets/images/icons/brand/br-telegram.svg'
import LN from '../../../assets/images/icons/brand/br-linkedin-sq.svg'
import TW from '../../../assets/images/icons/brand/br-twitter.svg'

import './HeaderTop.css'

const HeaderTop = () => {
    const [value, setValue] = useState(0);
    const [indicator, setIndicator] = useState('up');

    const onWheel = (e) => {
        const delta = value + Math.round(e.deltaY);
        setValue(delta)
        if (value > delta) {
            setIndicator('up')
        }
        else if (value < delta) {
            setIndicator('down')
        }
    };

    useEffect(() => {
        window.addEventListener("wheel", (e) => onWheel(e))
    })

    return (
        <div className={indicator === 'up'? 'header-top' : 'header-top header-top-hide'}>
            <div className="container header-top__container">
                <div className={'header-top__left'}>
                    <div className={'header-top__left__item'}>
                        <img src={phone} alt="phone"/>
                        <p>+996 (999) 99-99-99</p>
                    </div>
                    <div className={'header-top__left__item'}>
                        <img src={email} alt="email"/>
                        <p>corporate@mapef.kg</p>
                    </div>
                    <div className={'header-top__left__item'}>
                        <img src={location} alt="location"/>
                        <p>г. Бишкек, ул. Абдрахманова 137</p>
                    </div>
                </div>
                <div className={'header-top__right'}>
                    <img src={FB} alt="fb"/>
                    <img src={IG} alt="ig"/>
                    <img src={TG} alt="tg"/>
                    <img src={LN} alt="in"/>
                    <img src={TW} alt="tw"/>
                </div>
            </div>
        </div>
    );
};

export default HeaderTop;