import React from 'react';

import './ServicesDetailsList.css'

const ServicesDetailsList = ({data}) => {
    return (
        <ul>
            {data?.map((item, i) => {
                return (
                    <li key={i}>
                        <span>{item}</span>
                    </li>
                )
            })}
        </ul>
    );
};

export default ServicesDetailsList;