import React from 'react';


import logo from '../../../assets/images/Logo.png'

import FB from "../../../assets/images/icons/brand/br-fb-sq.svg"
import IG from '../../../assets/images/icons/brand/br-instagram.svg'
import TG from '../../../assets/images/icons/brand/br-telegram.svg'
import LN from '../../../assets/images/icons/brand/br-linkedin-sq.svg'
import TW from '../../../assets/images/icons/brand/br-twitter.svg'

import './FooterBrand.css'

const FooterBrand = () => {
    return (
        <div className={'footer__brand'}>
            <img src={logo} alt="logo"/>
            <p>
                Lörem ipsum od ohet dilogi. Bell trabel, samuligt, ohöbel utom diska. Jinesade bel när feras redorade i
                belogi. FAR paratyp i muvåning, och pesask vyfisat. Viktiga poddradio har un mad och inde.
            </p>
            <div className={'footer__brand-links'}>
                <img src={FB} alt="fb"/>
                <img src={IG} alt="ig"/>
                <img src={TG} alt="tg"/>
                <img src={LN} alt="in"/>
                <img src={TW} alt="tw"/>
            </div>
        </div>
    );
};

export default FooterBrand;