import React from 'react';
import {Swiper, SwiperSlide} from "swiper/react";
import {Keyboard, Mousewheel, Pagination} from "swiper";
import img1 from "../../../assets/images/slider/img1.png";
import img2 from "../../../assets/images/slider/img2.png";

import './CoverSlider.css'

const TestSlider = () => {
    return (
        <div className={'slider pt-100px'}>
            <Swiper
                pagination={{
                    clickable: true,
                    dynamicBullets: true,
                }}
                mousewheel={false}
                keyboard={true}
                autoplay={{
                    delay: 4500,
                    disableOnInteraction: false,
                }}
                modules={[ Pagination, Mousewheel, Keyboard]}
                className="mySwiper"

            >
                <SwiperSlide>
                    <div
                        className={'slider-item'}
                        style={{backgroundImage: `url(${img1})`}}
                    >
                        <div className="slider-item__box">
                            <p><span>Международная Ассоциация Партнёрской Экономики<br/>
                                и Финансов</span> — это площадка и мост<br/>
                                для предпринимателей, инвесторов, которые ведут<br/>
                                деятельность на отечественных и на международных<br/>
                                рынках.
                            </p>
                            <button>Подробнее</button>
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide>
                    <div
                        className={'slider-item-second'}
                        style={{backgroundImage: `url(${img2})`}}
                    >
                        <div className={'slider-item__box-second'}>
                            <h2>Международная Ассоциация Партнёрской Экономики и Финансов </h2>
                            <p>активно помогает в содействии развития исламского бизнеса на международных аренах</p>
                        </div>
                        <div className={'slider__btn-second'}>
                            <button>Подробнее</button>
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide>
                    <div
                        className={'slider-item'}
                        style={{backgroundImage: `url(${img1})`}}
                    >
                        <div className="slider-item__box">
                            <p>
                                <span>
                                    Международная Ассоциация Партнёрской Экономики<br/>
                                    и Финансов
                                </span>
                                — это площадка и мост<br/>
                                для предпринимателей, инвесторов, которые ведут<br/>
                                деятельность на отечественных и на международных<br/>
                                рынках.
                            </p>
                            <button>Подробнее</button>
                        </div>
                    </div>
                </SwiperSlide>
            </Swiper>
        </div>
    );
};

export default TestSlider;