import React from 'react';

import './ServicesDetailsPoster.css'

const ServicesDetailsPoster = ({title, description, img}) => {

    return (
        <section id={'services-poster'} className='pt-100px'>
            <div
                className={'poster-backgroundImage'}
                style={{backgroundImage: `url(${img})`}}
            >
                <div className="container">
                    <div className="services-poster__row">
                        <h2>{title}</h2>
                        <span>{description}</span>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default ServicesDetailsPoster;